<template>
  <div>
    <div class="atom-spinner" id="loaderc" v-if='!renderPage'>
      <div class="spinner-inner">
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-circle">
          &#9679;
        </div>
      </div>
    </div>
    <datetime auto :phrases="{cancel: 'Cancel'}" v-model="dat" v-if='renderPage'></datetime>
    <br>
    <div class="fancy-box-wrapper row" v-if='renderPage'>
    <div class="box-wrapper">
      <div class="bttn-warning" style="margin: 23% auto;" v-if='g == 0'>
        <h3>No Games For Today</h3>
      </div>
      <div class="col-lg-4 col-md-6 col-xl-4" v-for='(game, index) in games' v-if='renderPage && g > 0' :style="'display: inline-block;animation-delay: '+index*0.01+'s;'">
        <div class="box">
          <div class="title-wrapper">
            <div class="title">{{game.teams.away.team.teamName}} at {{game.teams.home.team.teamName}}</div>
          </div>
          <hr>
          <div class="button-wrapper animated fadeIn">
            <router-link :to="{ name: 'team', params: { id: game.teams.away.team.id }}">
              <img style="float: left;" v-bind:src="GetLogo(game.teams.away.team.abbreviation)+'&amp;h=55&amp;w=55'">
            </router-link>
            <div class="xs hint--rounded hint--bounce" style="margin-top: 20px;">
              <span class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline">{{game.status.abstractGameState == 'Live' || game.status.abstractGameState == 'Final' ? game.status.abstractGameState : LocalTime(game.gameDate)}}</span>
            </div>
            <router-link :to="{ name: 'team', params: { id: game.teams.home.team.id }}">
              <img style="float: right;" v-bind:src="GetLogo(game.teams.home.team.abbreviation)+'&amp;h=55&amp;w=55'">
            </router-link>
          </div>
          <router-link :to="{ name: 'game', params: { id: game.gamePk }}">
            <button class='bttn-float bttn-block bttn-xs bttn-warning'>WATCH</button>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
const axios = require('axios')
const {DateTime} = require("luxon");
export default {
  name: 'igames',
  data () {
    var local = DateTime.local().setZone("America/Los_Angeles")
    let dt = this.$route.params.date ? this.$route.params.date : local.toISODate()
    return {
      date:dt,
      games:{},
      dat:dt+'T00:00:00.000Z',
      g:null,
      renderPage:false
    }
  },
  mounted () {
    this.Load()
  },
  watch:{
    dat: function(dat){
      let datc = DateTime.fromISO(dat).toISODate()
      this.$router.push('/'+datc)
      this.Load(datc)
    }
  },
  methods: {
    Load(dat){
      let vm = this
      if(dat){
        this.date=dat
        this.renderPage = false
      }
      axios.get('https://statsapi.web.nhl.com/api/v1/schedule?startDate='+vm.date+'&endDate='+vm.date+'&expand=schedule.teams,schedule.linescore,schedule.broadcasts,schedule.ticket,schedule.game.content.media.epg&leaderCategories=&site=en_nhl&teamId=')
        .then(function (res) {
          vm.g = res.data.totalGames
          if(vm.g > 0 ){
          vm.games = res.data.dates[0].games
        }
        vm.renderPage = true
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    LocalTime(x){
      return LocalTime(x);
    },
    GetLogo(x){
      return GetLogo(x);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .card .card-body .form-group {margin: 2rem 0;}
    button.bttn-float.bttn-xs.bttn-warning.my-4 {
    width: 32%;
    margin: 0.125rem;
    padding: 0.5rem;
    font-weight: 600;
    font-size: 13px;
    }
</style>



