<template>
  <div>
    <div class="bttn-warning autocen" v-if='error'>
     <h2>Invalid Game</h2><br/>
    </div>
    <div class="atom-spinner" v-else-if='!renderPage'>
      <div class="spinner-inner">
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-circle">
          &#9679;
        </div>
      </div>
    </div>
    <div v-else>
      <div class="bttn-warning">
       <h2>{{game.teams.away.team.name}} at {{game.teams.home.team.name}}</h2><br/>
      </div>
      <div id="headx" v-if='!timer'>
        <!--<label style="margin: 0 0.2rem 0 1rem;color: #feab3a;">Cdn : </label>-->
        <select v-model="cdn" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" data-toggle="tooltip"title="Cdn">
          <option value="akc">Akamai</option>
          <option value="l3c">Level 3</option>
        </select>
        <select v-model="feed" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" data-toggle="tooltip"title="Feed">
          <option v-for='(media,index) in game.content.media.epg[0].items' :value="media.mediaPlaybackId" >Feed : {{media.mediaFeedType}} {{media.callLetters}}</option>
        </select>
        <button v-on:click="refresh()" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" data-toggle="tooltip"title="Refresh">
          Refresh <i class="material-icons" style="vertical-align: middle;">refresh</i>
        </button>
      </div>
      <div class="card" style="background-color: transparent;box-shadow: none;">
  	    <div class="row">
  		    <div class="col-xl col-lg-12" style="" id="flw" style="min-height: 390px;margin-bottom: 1rem;">
            <div class="atom-spinner" id="loaderc" v-if='!play && !timer'>
              <div class="spinner-inner">
                <div class="spinner-line"></div>
                <div class="spinner-line"></div>
                <div class="spinner-line"></div>
                <div class="spinner-circle">&#9679;</div>
              </div>
            </div>
            <timer v-else-if='timer' :id="gamedate"></timer>
            <player v-else :player="player" :channel="vidsrc"></player>
  		    </div>
          <div class="col-xl-4 col-lg-12" id="chatbox" style="min-height: 390px;margin-bottom: 1rem;" v-if='chat=="on" && !timer'>
            <chat :channel="chatid"></chat>
				  </div>
  	    </div>
      </div>
    </div>
  </div>
</template>

<script>
import player from '@/components/player'
import chat from '@/components/chat'
import timer from '@/components/timer'
const axios = require('axios')
export default {
  name: 'game',
  components: { chat,player,timer },
  data () {
    return {
      chatid:'streamsripnhl',
      date:null,
      game:{},
      gamedate:null,
      id:this.$route.params.id,
      cdn:this.$route.params.cdn,
      feed:this.$route.params.feed,
      vidsrc:null,
      play:false,
      renderPage:false,
      error:false,
      timer:true,
      player: localStorage.player,
      chat:localStorage.chat
    }
  },
  mounted(){
    this.Load()
  },
  watch:{
    cdn: function(){
      this.play = false
      this.$router.push('/game/'+this.id+'/'+this.cdn+'/'+this.feed)
      this.loadsrc()
    },
    feed: function(){
      this.play = false
      this.$router.push('/game/'+this.id+'/'+this.cdn+'/'+this.feed)
      this.loadsrc()
    }
  },
  methods: {
    Load(){
      let vm = this
      axios.get('https://statsapi.web.nhl.com/api/v1/schedule?gamePk='+vm.id+'&expand=schedule.teams,schedule.linescore,schedule.broadcasts,schedule.ticket,schedule.game.content.media.epg&leaderCategories=&site=en_nhl&teamId=')
        .then(function (res) {
          vm.g = res.data.totalGames
          vm.date = res.data.dates[0].date
          vm.game = res.data.dates[0].games[0]
          vm.gamedate = res.data.dates[0].games[0].gameDate
          if(!vm.feed)
          vm.feed = vm.game.content.media.epg[0].items[0].mediaPlaybackId
          if(!vm.cdn)
          vm.cdn = 'akc'
          vm.loadsrc()
        })
        .catch(function (err) {
          vm.error = true
          //console.log(err)
        })
    },
    loadsrc(){
      let vm = this
      axios.get('https://cdn.streams.rip/m3u8/'+vm.date+'/'+vm.feed+vm.cdn)
        .then(function (res) {
          vm.vidsrc = res.data
          vm.timer = false
          vm.play = true
          vm.renderPage = true
        })
        .catch(function (err) {
        vm.renderPage = true
          //console.log(err)
        })
    },
    refresh(){
      this.play = false
      this.$router.push('/game/'+this.id+'/'+this.cdn+'/'+this.feed)
      this.loadsrc()
    }
  }
}
</script>

<style scoped>
  #cdn,#feed,#chat {margin: 0.32rem 0.96rem;}h2 {color : #f4af3e}
  	.form-check .form-check-sign {
      vertical-align: middle;
      position: relative;
      top: 2px;
      float: left;
      padding-right: 0px;
      display: inline-block;
      padding-left: 8px;
  }.form-check {margin-bottom: 0;}
  #rmpPlayer {border-radius: 6px;}.lds-facebook {margin: 0 auto;}#tEa3-dimensions {width: 100%;height: 480px;}.dark-edition .form-control, .is-focused .dark-edition .form-control {background-image: linear-gradient(to top, #429eb2 2px, rgba(156, 39, 176, 0) 2px), linear-gradient(to top, rgba(180, 180, 180, 0.1) 1px, rgba(180, 180, 180, 0) 1px);}.card .card-body {padding: 1rem;}.btn {width: 32%;margin: 0.125rem;padding: 0.5rem;font-size: small;font-weight: 600;}button#oki,input#yt {margin: 15px;}input#yt {text-align: center;}.card-img-top{border-radius: 6px;}.card-header {padding: 0;}.main-panel {width: calc(100%);}.main-panel>.content {margin-top: 0;padding: 0;}nav.card.footer {margin-top: 10px;}footer.card.footer {margin-bottom: 10px;}h4.titl {padding-top: 1.5rem;}.dark-edition .btn.btn-info {color: black;}#dli{color: #fff;}
 #loaderc {
    height: 72px;
    width: 72px;
    overflow: hidden;
    margin: 20% auto;
  }
</style>



