<template>
  <div>
    <div class="bttn-warning" v-if='error'>
     <h2>Invalid Game</h2><br/>
    </div>
    <div class="atom-spinner" id="loaderc" v-else-if='!renderPage'>
      <div class="spinner-inner">
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-circle">
          &#9679;
        </div>
      </div>
    </div>
    <div v-else>
      <div class="bttn-warning">
       <h2>{{homed.name}} vs {{awayd.name}}</h2><br/>
      </div>
      <div id="headx">
        <select v-model="feed" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" data-toggle="tooltip"title="Quality">
          <option v-for='(media,index) in data' v-if="index !='worst'" :value="index">Quality : {{index}}</option>
        </select>
        <button v-on:click="refresh()" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" data-toggle="tooltip"title="Refresh">
          Refresh <i class="material-icons" style="vertical-align: middle;">refresh</i>
        </button>
      </div>
      <div class="card" style="background-color: transparent;box-shadow: none;">
  	    <div class="row">
  		    <div class="col-xl col-lg-12" style="" id="flw" style="min-height: 390px;margin-bottom: 1rem;">
            <div class="atom-spinner" id="loaderc" v-if='!play'>
              <div class="spinner-inner">
                <div class="spinner-line"></div>
                <div class="spinner-line"></div>
                <div class="spinner-line"></div>
                <div class="spinner-circle">&#9679;</div>
              </div>
            </div>
            <player v-else :player="player" :channel="vidsrc"></player>
  		    </div>
          <div class="col-xl-4 col-lg-12" id="chatbox" style="min-height: 390px;margin-bottom: 1rem;">
            <chat :channel="chatid"></chat>
				  </div>
  	    </div>
      </div>
    </div>
  </div>
</template>

<script>
import player from '@/components/player'
import chat from '@/components/chat'
const axios = require('axios')
export default {
  name: 'mvideo',
  components: { player,chat },
  data () {
    return {
      chatid:'streamsrip',
      data:null,
      game:{},
      vid:this.$route.params.vid,
      home:this.$route.params.home,
      away:this.$route.params.away,
      homed:null,
      awayd:null,
      id:(this.$route.params.vid+this.$route.params.home+this.$route.params.away),
      key:0,
      vidsrc:null,
      play:false,
      player: 'flow',
      feed:this.$route.params.feed,
      renderPage:false,
      error:false
    }
  },
  mounted(){
    this.Load()
  },
  watch:{
    feed: function(){
      this.play = false
      this.$router.push('/video/'+this.vid+'/'+this.home+'/'+this.away+'/'+this.feed)
    }
  },
  methods: {
    Load(){
      let vm = this
      if (localStorage.getItem(vm.id)) {
        let dt = JSON.parse(localStorage.getItem(vm.id))
        vm.data = dt['data']
        vm.homed = dt['home']
        vm.awayd = dt['away']
        if(!vm.feed || !vm.data[vm.feed]){vm.feed = 'best'}
        vm.vidsrc = vm.data[vm.feed]
        vm.play = true
        vm.renderPage = true
      }
      else{
        axios.get('http://167.99.232.150/vid.php?url='+vm.vid)
        .then(function (res) {
          vm.data = res.data
          axios.get('http://api.football-data.org/v1/teams/'+vm.home).then(function(res){
            vm.homed = res.data
            axios.get('http://api.football-data.org/v1/teams/'+vm.away).then(function(res){
              vm.awayd = res.data
              if(!vm.feed || !vm.data[vm.feed])
              vm.feed = 'best'
              const gm = JSON.stringify({"data":vm.data,"home":vm.homed,"away":vm.awayd})
              localStorage.setItem(vm.id,gm)
              vm.vidsrc = vm.data[vm.feed]
              vm.play = true
              vm.renderPage = true
            }).catch(function(err){
              vm.error = true
              console.log(err)})
          })
          }).catch(function(err){
            vm.error = true
            console.log(err)})
        .catch(function (err) {
          vm.error = true
          console.log(err)
        })
      }
    },
    refresh(){
      this.play = false
      this.$router.push('/video/'+this.vid+'/'+this.home+'/'+this.away+'/'+this.feed)
      this.play = true
    }
  }
}
</script>

<style scoped>
  #cdn,#feed,#chat {margin: 0.32rem 0.96rem;}h2 {color : #f4af3e}
  	.form-check .form-check-sign {
      vertical-align: middle;
      position: relative;
      top: 2px;
      float: left;
      padding-right: 0px;
      display: inline-block;
      padding-left: 8px;
  }.form-check {margin-bottom: 0;}
  #rmpPlayer {border-radius: 6px;}.lds-facebook {margin: 0 auto;}#tEa3-dimensions {width: 100%;height: 480px;}.dark-edition .form-control, .is-focused .dark-edition .form-control {background-image: linear-gradient(to top, #429eb2 2px, rgba(156, 39, 176, 0) 2px), linear-gradient(to top, rgba(180, 180, 180, 0.1) 1px, rgba(180, 180, 180, 0) 1px);}.card .card-body {padding: 1rem;}.btn {width: 32%;margin: 0.125rem;padding: 0.5rem;font-size: small;font-weight: 600;}button#oki,input#yt {margin: 15px;}input#yt {text-align: center;}.card-img-top{border-radius: 6px;}.card-header {padding: 0;}.main-panel {width: calc(100%);}.main-panel>.content {margin-top: 0;padding: 0;}nav.card.footer {margin-top: 10px;}footer.card.footer {margin-bottom: 10px;}h4.titl {padding-top: 1.5rem;}.dark-edition .btn.btn-info {color: black;}#dli{color: #fff;}
  #loaderc {
    height: 72px;
    width: 72px;
    overflow: hidden;
    margin: 20% auto;
  }
</style>



