<template>
  <iframe
    :src="src"
    :width="width"
    :height="height"
    style="min-height: 390px;border: 1px #051925 solid;border-radius: 5px;"
    :frameborder="frameBorder"
    :scrolling="scrolling"
  ></iframe>
</template>

<script>
  export default {
    name: 'twitch-chat',
    props: {
      channel: {
        type: String,
        required: true,
      },
      frameBorder: {
        type: Number,
        default: 0,
      },
      width: {
        type: String,
        default: '100%',
      },
      height: {
        type: String,
        default: '100%',
      },
      scrolling: {
        type: Boolean,
        default: true,
      },
    },
    computed: {
      src() {
        return `https://www.twitch.tv/embed/${this.channel}/chat?darkpopout`;
      },
    },
  };
</script>



