import Vue from 'vue'
import Router from 'vue-router'
import igames from '@/components/igames'
import game from '@/components/game'
import team from '@/components/team'
import video from '@/components/video'
import Datetime from 'vue-datetime'
import 'vue-datetime/dist/vue-datetime.css'
Vue.use(Datetime)
Vue.extend({
  template: '<datetime/>',
  components: {
    datetime: Datetime
  }
});

Vue.use(Router)

export default new Router({
  //mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/:date?',
      name: 'igames',
      component: igames
    },
    {
      path: '/team/:id',
      name: 'team',
      component: team
    },
    {
      path: '/game/:id/:cdn?/:feed?',
      name: 'game',
      component: game
    },
    {
      path: '/video/:vid/:home/:away/:feed?',
      name: 'video',
      component: video
    }
  ]
})



