<template>
  <div id="app">
    <div :class="theme+'-edition main-panel'">
      <div class="content">
        <div class="container-fluid">
          <div class="row">
            <div class="col-xl-8 col-md-12 col-lg-12" style="margin: 0 auto;">
              <div class="container-fluid">
                <nav class="card footer">
            			 <ul class="container-fluid float-center">
                     <li v-if="$route.path !='/'" >
                       <a @click="$router.back()"  data-toggle="tooltip"title="Go Back!">
                         <i class="material-icons" style="color:#feab3a;cursor: pointer; "">arrow_back</i>
                       </a>
                     </li>
                     <li>
                       <router-link to="/">
                         Home
                       </router-link>
                     </li>
                     <!--<li>
                       <router-link :to="{ name: 'team', params: { id: 1 }}">
                         MLB
                       </router-link>
                     </li>-->
                     <li><a href="#">MLB</a></li>
                     <li><a href="#">NHL</a></li>
                     <li>
                      	<a href="#" class="bttn-block bttn-xs bttn-warning" data-toggle="modal" data-target="#settings">
                          <i class="material-icons" style="vertical-align: middle;">settings</i>
                      	</a>
                     </li>
                   </ul>
              	</nav>
                <router-view id="cntr"/>
                <footer class="card footer">
            		    <ul class="container-fluid float-center">
                      <li><a href="https://streams.rip">Streamsrip</a></li>
                      <li><a href="https://streams.rip/">About Us</a></li>
                      <li><a href="https://streams.rip/">TOS</a></li>
            		    </ul>
                </footer>
                <div class="modal fade" id="settings" tabindex="-1" role="dialog" aria-labelledby="exampleModalLongTitle" aria-hidden="true">
                  <div class="modal-dialog" role="document">
                    <div class="modal-content box" style="margin-top: 50%;">
                      <div class="modal-header" style="border-bottom: 1px solid #f4aa39">
                        <h5 class="title text-center" style="width: 100%;" id="exampleModalLongTitle">Settings</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                      </div>
                      <div class="modal-header">
                        <h5 class="title" id="exampleModalLongTitle">Theme</h5>
                        <select v-model="theme" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" data-toggle="tooltip"title="Cdn">
                          <option value="dark">Dark Theme</option>
                          <option value="light">Light Theme</option>
                        </select>
                      </div>
                      <div class="modal-header">
                        <h5 class="title" id="exampleModalLongTitle">Player</h5>
                        <select v-model="player" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" data-toggle="tooltip"title="Cdn">
                          <option value="flow">flow player</option>
                          <option value="clappr">clappr player</option>
                        </select>
                      </div>
                      <div class="modal-header">
                        <h5 class="title" id="exampleModalLongTitle">Chat</h5>
                        <select v-model="chat" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" data-toggle="tooltip"title="Cdn">
                          <option value="on">Active</option>
                          <option value="off">Inactive</option>
                        </select>
                      </div>
                    </div>
                  </div>
                </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    if (!localStorage.theme) {
      localStorage.theme = 'dark';
    }
    if (!localStorage.player) {
      localStorage.player = 'flow';
    }
    if (!localStorage.chat) {
      localStorage.chat = 'on';
    }
    return {
      theme: localStorage.theme,
      player: localStorage.player,
      chat:localStorage.chat
    }
  },
  watch: {
    theme(theme) {
      localStorage.theme = theme;
    },
    player(player) {
      localStorage.player = player;
    },
    chat(chat) {
      localStorage.chat = chat;
    }
  }
}
</script>

<style>
#app {
}
</style>



