<template>
  <iframe
    :src="src"
    :width="width"
    :height="height"
    style="min-height: 390px;"
    :frameborder="frameBorder"
    scrolling="no"
    allowfullscreen
  ></iframe>
</template>

<script>
  export default {
    name: 'twitch-chat',
    props: {
      channel: {
        type: String,
        required: true,
      },
      player: {
        type: String,
        required: true,
        default: 'flow',
      },
      frameBorder: {
        type: Number,
        default: 0,
      },
      width: {
        type: String,
        default: '100%',
      },
      height: {
        type: String,
        default: '100%',
      }
    },
    computed: {
      src() {
        return `http://streams.rip/p/${this.player}.html?id=${this.channel}`;
      },
    },
  };
</script>



