<template>
  <div>
    <div class="atom-spinner" id="loaderc" v-if='!renderPage && !error'>
      <div class="spinner-inner">
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-circle">
          &#9679;
        </div>
      </div>
    </div>
    <div class="bttn-warning" v-if='error'>
     <h2>Invalid Team</h2><br/>
    </div>
    <div v-if='renderPage'>
    <div class="bttn-warning">
      <h2>{{team.name}}</h2><br/>
    </div>
    <div class="fancy-box-wrapper">
     <div class="row">
       <div class="col-md-4">
         <div class="card card-profile">
           <div class="card-avatar">
             <img v-bind:src="GetLogo(team.abbreviation)+'&amp;h=130&amp;w=130'">
           </div>
           <div class="card-body">
             <h4 class="card-title bttn-warning"><a v-bind:href="team.officialSiteUrl" class="bttn-warning">{{team.name}}</a></h4>
               <h6 class="card-category i">Games Played <i>{{stats[0].splits[0].stat.gamesPlayed}}</i></h6>
               <h6 class="card-category i">Points <i>{{stats[0].splits[0].stat.pts}} - {{stats[1].splits[0].stat.pts}} - {{stats[0].splits[0].stat.ptPctg}}%</i></h6>
               <h6 class="card-category i">Wins <i>{{stats[0].splits[0].stat.wins}} - {{stats[1].splits[0].stat.wins}}</i></h6>
               <h6 class="card-category i">Losses <i>{{stats[0].splits[0].stat.losses}} - {{stats[1].splits[0].stat.losses}}</i></h6>
               <h6 class="card-category i">OT <i>{{stats[0].splits[0].stat.ot}} - {{stats[1].splits[0].stat.ot}}</i></h6>
               <h6 class="card-category i">Goals Per Game <i>{{stats[0].splits[0].stat.goalsPerGame}} - {{stats[1].splits[0].stat.goalsPerGame}}</i></h6>
               <h6 class="card-category i">Goals Ag Per Game <i>{{stats[0].splits[0].stat.goalsAgainstPerGame}} - {{stats[1].splits[0].stat.goalsAgainstPerGame}}</i></h6>
           </div>
         </div>
       </div>
       <div class="col-md-8">
         <div class="col-xl-6 col-lg-6 col-md-6 col-sm-6" v-for='leader in leaders' style="float: right;">
           <div class="card card-stats">
             <div class="card-header card-header-warning card-header-icon">
               <div class="card-icon">
                 <img v-bind:src="'https://nhl.bamcontent.com/images/headshots/current/168x168/'+leader.leaders[0].person.id+'.png'">
               </div>
               <p class="card-category" style="font-size: 13px;white-space: nowrap;">{{leader.leaders[0].person.fullName}}</p>
               <h3 class="card-title" style="white-space: nowrap;">{{leader.leaders[0].value}}
                 <small>{{leader.leaderCategory}}</small>
               </h3>
             </div>
             <div class="card-footer">
               <div class="stats">
                   <h6 class="card-category">{{leader.leaders[0].person.primaryPosition.name}}</h6>
               </div>
               <div class="stats" v-if='leader.leaders[0].person.alternateCaptain'>
                   <h6 class="card-category">Alt Captain</h6>
               </div>
               <div class="stats" v-else-if='leader.leaders[0].person.captain'>
                   <h6 class="card-category">Captain</h6>
               </div>
               <div class="stats">
                   <h6 class="card-category">#{{leader.leaders[0].person.primaryNumber}}</h6>
               </div>
               <div class="stats">
                   <h6 class="card-category">{{leader.leaders[0].person.nationality}}</h6>
               </div>
             </div>
           </div>
         </div>
       </div>
     </div>
     <div class="bttn-warning">
       <h3>Games Archive 2018/2019</h3><br/>
     </div>
     <div id="games" class="box-wrapper">
       <div class="col-lg-4 col-md-6 col-xl-4" v-for='(game,index) in games' :style="'display: inline-block;animation-delay: '+index*0.01+'s;'">
           <div class="box">
             <div class="title-wrapper"><div class="title">{{game.games[0].teams.away.team.teamName}} at {{game.games[0].teams.home.team.teamName}}</div>
           </div>
           <hr>
           <div class="button-wrapper animated fadeIn">
             <router-link :to="{ name: 'team', params: { id: game.games[0].teams.away.team.id }}">
               <img style="float: left;" v-bind:src="GetLogo(game.games[0].teams.away.team.abbreviation)+'&amp;h=55&amp;w=55'">
             </router-link>
             <div class="xs hint--rounded hint--bounce">
               <button class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" v-if='game.games[0].status.abstractGameState == "Final"'>{{game.games[0].teams.away.score}}-{{game.games[0].teams.home.score}}</button>
               <button class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline">{{game.date}}</button>
               <button class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" v-if='game.games[0].status.abstractGameState != "Final"'>{{LocalTime(game.games[0].gameDate)}}</button>
             </div>
             <router-link :to="{ name: 'team', params: { id: game.games[0].teams.home.team.id }}">
               <img style="float: right;" v-bind:src="GetLogo(game.games[0].teams.home.team.abbreviation)+'&amp;h=55&amp;w=55'">
             </router-link>
           </div>
           <router-link :to="{ name: 'game', params: { id: game.games[0].gamePk }}">
             <button class='bttn-float bttn-block bttn-xs bttn-warning'>WATCH</button>
           </router-link>
       </div>
     </div>
     </div>
    </div>
    </div>
  </div>
</template>
<script>
const {DateTime} = require("luxon");
const axios = require('axios')
export default {
  name: 'team',
  data () {
    return {
      team: [],
      leaders: [],
      stats:[],
      games: [],
      id:this.$route.params.id,
      renderPage: false,
      error:false
    }
  },
  watch:{
    $route: function(id){
      this.renderPage=false
      this.Load(this.$route.params.id)
    }
  },
  mounted () {
    this.Load()
  },
  methods: {
    Load(id){
      let vm = this
      if(id){vm.id=id}
      if (localStorage.getItem(vm.id)) {
          vm.team = JSON.parse(localStorage.getItem(vm.id))
        }
      else{
      axios.get('https://statsapi.web.nhl.com/api/v1/teams/'+vm.id)
        .then(function (res) {
          vm.team = res.data.teams[0]
          const gm = JSON.stringify(vm.team)
          localStorage.setItem(vm.id,gm)
        })
        .catch(function (err) {
          vm.error = true
          console.log(err)
        })
      }
      axios.get('https://statsapi.web.nhl.com/api/v1/teams/'+vm.id+'/stats')
        .then(function (res) {
          vm.stats = res.data.stats
        })
        .catch(function (err) {
          console.log(err)
        })
      axios.get('https://statsapi.web.nhl.com/api/v1/teams/'+vm.id+'?expand=team.leaders,leaders.person&leaderGameTypes=R&leaderCategories=goals,assists,points,wins&season=20182019')
          .then(function (res) {
            vm.leaders = res.data.teams[0].teamLeaders
          })
          .catch(function (err) {
            console.log(err)
          })
      let date = DateTime.local().toISODate();
      axios.get('https://statsapi.web.nhl.com/api/v1/schedule?startDate=2018-10-04&endDate='+date+'&hydrate=team(leaders(categories=[points,goals,assists],gameTypes=[R])),linescore,broadcasts(all),tickets,game(content(media(epg),highlights(scoreboard)),seriesSummary),radioBroadcasts,metadata,decisions,scoringplays,seriesSummary(series)&site=en_nhl&teamId='+vm.id+'&season=20182019&timecode=')
        .then(function (res) {
          vm.games = res.data.dates
          vm.renderPage = true
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    LocalTime(x){
      return LocalTime(x);
    },
    GetLogo(x){
      return GetLogo(x);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h6 i {
    float: right;
    margin: 0 0.25rem;
    color: #eca535;
  }
  .card-icon img {
    width: 70px;
  }
  h6.i {
      text-align: left;
      margin-left: 1.2rem;
  }
  .card-profile .card-avatar {background: #051724;}
  .dark-edition .card .card-header-warning .card-icon, .dark-edition .card .card-header-warning .card-text, .dark-edition .card .card-header-warning:not(.card-header-icon):not(.card-header-text), .dark-edition .card.bg-warning, .dark-edition .card.card-rotate.bg-warning .front, .dark-edition .card.card-rotate.bg-warning .back {
      background: linear-gradient(120deg,#051925,#f4aa39);
  }
  .card [class*=card-header-] .card-icon, .card [class*=card-header-] .card-text {
      border-radius: 3px;
      background-color: #999;
      padding: 5px;
      margin-top: -20px;
      margin-right: 15px;
      float: left;
  }
</style>



