<template>
<div id="timer" class="timer">
  <div class="bttn-warning"><h3 style="color: #f4af3e;">Game Starts In</h3></div>
  <br><br>
    <div class="day">
      <span class="number">{{ dys }}</span>
      <div class="format">Days</div>
    </div>
    <div class="hour">
      <span class="number">{{ hrs }}</span>
      <div class="format">Hours</div>
    </div>
    <div class="min">
      <span class="number">{{ min }}</span>
      <div class="format">Minutes</div>
    </div>
    <div class="sec">
      <span class="number">{{ sec }}</span>
      <div class="format">Seconds</div>
    </div>
    <br><br>
    <div class="bttn-warning"><h3 style="color: #f4af3e;">Enjoy Your Time</h3></div>
  </div>
</template>

<script>
  export default {
    name: 'timer',
    props: {
      id: {
        type: String,
        required: true,
      }
    },
    data () {
      return {
        dys:'',
        hrs:'',
        min:'',
        sec:'',
        interval: '',
        ctd:new Date(this.id).getTime()
      }
    },
    mounted(){
      this.timerCount(this.ctd)
      this.interval = setInterval(() => {
          this.timerCount(this.ctd);
      }, 1000)
    },
    methods:{
      timerCount: function(start){
        var now = new Date().getTime()
        var distance = start - now
        if(distance < 0 ){
            clearInterval(this.interval)
            this.$parent.timer = false
            this.$parent.refresh()
            return
        }
        this.calcTime(distance)
      },
      calcTime: function(dist){
          this.dys = Math.floor(dist / (1000 * 60 * 60 * 24));
          this.hrs = Math.floor((dist % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
          this.min = Math.floor((dist % (1000 * 60 * 60)) / (1000 * 60));
          this.sec = Math.floor((dist % (1000 * 60)) / 1000);
      }
    }
  };
</script>
<style scoped>
  .timer {
 font-size: 20px;
 color: #fff;
 text-align:center;
 margin-top: 50px;
}
     .day, .hour, .min, .sec {
       font-size: 30px;
       display: inline-block;
       font-weight: 500;
       text-align: center;
       margin: 0 5px;}
       .dark-edition .format {
         font-weight: 300;
         font-size: 14px;
         opacity: 0.8;
         width: 60px;
         color: white;
       }
     .dark-edition .number {
    background: rgb(5, 25, 37);
    padding: 7px;
    border-radius: 5px;
    display: inline-block;
    width: 60px;
    text-align: center;
    color: #f4aa39;
        box-shadow: 0 1px 4px 0 rgba(0,0,0,.14);
     }
     .format {
       font-weight: 300;
       font-size: 14px;
       opacity: 0.8;
       width: 60px;
       color: black;
     }
   .number {
  background: white;
  padding: 7px;
  border-radius: 5px;
  display: inline-block;
  width: 60px;
  text-align: center;
  color: #f4aa39;
      box-shadow: 0 1px 4px 0 rgba(0,0,0,.14);
   }
     .message {
       font-size: 14px;
       font-weight: 400;
       margin-top: 5px;
     }
     .status-tag{
       width: 270px;
       margin: 10px auto;
       padding: 8px 0;
       font-weight: 500;
       color: #000;
       text-align: center;
       border-radius: 15px;
     }
       &.upcoming{
         background-color: lightGreen;
       }
       &.running{
         background-color: gold;
       }
       &.expired{
         background-color: silver;
       }
</style>



